<template>
  <div id="QualityAssessment">
    <Form :label-width="100" class="searchesBox" id="humanPc">
      <FormItem prop="province" label="人类活动批次：">
        <Button type="success" size="small" @click="activitiesModal = true" v-model="activitiesValue"  v-html="activitiesContent" style="margin-right: 10px"></Button>
      </FormItem>
      <div class="queryBtns" style="float: right;margin-top: 6px;">
        <Button type="primary" size="small" icon="md-cloud-download" @click="handleExport" class="exportBtn">导出</Button>
      </div>
    </Form>
    <div class="assessmentBox">
      <div class="left">
        <Table border stripe :loading="loading" :columns="columns1"
               :data="data1" :maxHeight="tableHeight"></Table>
      </div>
      <div class="right">
        <BarChart :data="data1"></BarChart>
        <Spin size="large" fix v-if="spinShow"></Spin>
      </div>
    </div>
    <!-- 人类活动列表-->
    <Modal
      v-model="activitiesModal"
      title="人类活动批次列表"
      @on-cancel="handleCancel"
      width="1000">
      <div>
        <Table border stripe :loading="aLoading" :columns="aColumns1"
               :data="aData1" highlight-row :height="aTableHeight"
               ref="activitiesMultipleTable"
               @on-current-change="changeAccount"
               id="reportAnalysisTable"></Table>
        <div class="page_box" style="width: 100%;padding: 15px;text-align: right">
          <Page :total="aTotal" :page-size="aPageSize" :current.sync="aPageNum" @on-change="aPageChange" show-total/>
        </div>
      </div>
      <div slot="footer">
        <i-button type="text" @click="handleCancel" class="mr10">取消</i-button>
        <Button type="primary" @click="selectActivities">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import BarChart from './BarChart.vue'
import {
  // 人类活动列表
  getList
} from '@/api/reportAnalysis'
import {
  // 人类活动巡查结果质量评估列表
  getAssessmentList
} from '@/api/quality'
export default {
  name: 'QualityAssessment',
  data () {
    return {
      // 选中人类活动批次id
      activitiesValue: '',
      // 选中人类活动批次name
      activitiesContent: '请选择人类活动批次',
      // 人类活动列表弹窗
      activitiesModal: false,
      /* 表格高度 */
      aTableHeight: Math.floor((window.innerHeight - 300 - 70) / 48) * 48 + 40,
      /* 表格数据 */
      aColumns1: [
        // {
        //   type: 'selection',
        //   width: 60,
        //   align: 'center'
        // },
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.aPageNum - 1) * this.aPageSize + 1)
          }
        },
        {
          title: '任务名称',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '说明',
          align: 'center',
          minWidth: 250,
          key: 'remark'
        },
        {
          title: '底图服务',
          align: 'center',
          width: 300,
          key: 'url'
        }
      ],
      aData1: [],
      /* 总条数 */
      aTotal: 0,
      aPageNum: 1,
      aPageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      aLoading: false,
      /* 当前选中表格行数据 */
      currentRow: null,
      /* 查询列表 */
      /* 表格高度 */
      tableHeight: window.innerHeight - 252,
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '活动设施类型名称',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '原始数量',
          align: 'center',
          minWidth: 150,
          key: 'originNum'
        },
        {
          title: '当前数量',
          align: 'center',
          minWidth: 150,
          key: 'todayNum'
        }
      ],
      data1: [],
      /* 加载 */
      loading: false,
      spinShow: false
    }
  },
  components: {
    BarChart
  },
  created () {
    /* 获取人类活动列表信息 */
    this.getTableList()
  },
  methods: {
    /* axios: 获取人类活动列表数据 */
    getTableList () {
      this.aLoading = true
      getList({
        data: {
          pageSize: this.aPageSize,
          pageNum: this.aPageNum
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.aData1 = res.data.data.rows
          this.aTotal = res.data.data.total
          this.aLoading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 人类活动列表分页跳转 */
    aPageChange (pageNum) {
      this.aPageNum = pageNum
      this.getTableList()
    },
    /* 取消选择人类活动 */
    handleCancel () {
      this.$refs.activitiesMultipleTable.clearCurrentRow()
      this.activitiesModal = false
    },
    // 保存勾选人类活动
    selectActivities () {
      this.activitiesContent = this.currentRow.name
      this.activitiesValue = this.currentRow.id
      this.handleCancel()
      // 根据条件查询发生变化斑块列表
      this.getResultList(this.currentRow.id)
    },
    /* 选中台账的函数 */
    changeAccount (currentRow, oldCurrentRow) {
      this.currentRow = currentRow
    },
    /* axios: 根据条件查询列表 */
    getResultList (e) {
      this.spinShow = true
      this.loading = true
      getAssessmentList({
        data: {
          id: e // 人类活动标识
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = []
          if (res.data.data.data.length > 0) {
            res.data.data.data.forEach((item, index) => {
              this.data1.push({
                name: item.sysCompany.comName,
                originNum: item.st4ScsCd.checkNum,
                todayNum: item.st4ScsCd.uncheckNum
              })
            })
          }
          this.spinShow = false
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  #QualityAssessment {
    padding: 10px;
    width: 100%;
    height: 100%;
    .searchesBox {
      width: 49%;
      overflow: hidden;
      .ivu-form-item {
        margin-bottom: 0;
        float: left;
      }
      margin-bottom: 10px;
    }
    .assessmentBox {
      width: 100%;
      height: calc(100% - 42px);
      overflow: hidden;
      .left {
        float: left;
        width: 49%;
        margin-left: 5px;
        height: calc(100% - 5px);
      }
      .right {
        position: relative;
        float: right;
        width: 49%;
        height: calc(100% - 5px);
        margin-right: 5px;
      }
      .exportBtn {
        background: #2d579b;
        border-color: #2d579b;
        margin-right: 10px;
        &:hover {
          background: #1a3a6f;
          border-color: #1a3a6f;
        }
      }
      .queryBtns {
        float: right;
        margin-top: 6px;
      }
    }
  }
</style>
